<script>
import { defineComponent, h } from 'vue'
import { mixinColor } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

export default defineComponent({
  name: 'Badge',
  mixins: [
    ...addMixinPrefix([
      mixinColor
    ], 'badge')
  ],
  setup(props, { slots }) {
    const genContent = () => {
      return h('span', {
        class: 'badge__content'
      }, slots.default())
    }

    return {
      genContent
    }
  },
  render() {
    const children = [
      this.genContent()
    ]
    const listClass = [
      'badge',
      this.classColor
    ]

    return h('div', {
      class: listClass
    }, children)
  }
})
</script>